import { rejects } from "assert"
import { resolve } from "path"

//es6
//
export default(params,str)=>{
    //
    uni.showLoading({
        title: str,
    });
    return new Promise((resolve,rejects)=>{
         uni.request({
            ...params,
            //
            success: (result)=>{
                resolve(result.data);
            },
            //
            fail: (err)=>{
                rejects(err);
            },
            //
            complete: ()=>{
               uni.hideLoading();
            }
        });
    })
}